interface ClaudeMessage {
  role: 'user' | 'assistant';
  content: string;
}

interface ClaudeRequest {
  model: string;
  max_tokens: number;
  messages: ClaudeMessage[];
}

interface ClaudeResponse {
  content: Array<{
    type: 'text';
    text: string;
  }>;
  usage: {
    input_tokens: number;
    output_tokens: number;
  };
}

export async function callClaudeAPI(prompt: string): Promise<string> {
  const apiKey = process.env.ANTHROPIC_API_KEY;
  
  if (!apiKey) {
    throw new Error('ANTHROPIC_API_KEY environment variable is not set');
  }

  const requestBody: ClaudeRequest = {
    model: 'claude-3-5-sonnet-20241022',
    max_tokens: 1000,
    messages: [
      {
        role: 'user',
        content: prompt
      }
    ]
  };

  try {
    const response = await fetch('https://api.anthropic.com/v1/messages', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'x-api-key': apiKey,
        'anthropic-version': '2023-06-01'
      },
      body: JSON.stringify(requestBody)
    });

    if (!response.ok) {
      const errorText = await response.text();
      throw new Error(`Claude API error: ${response.status} - ${errorText}`);
    }

    const data: ClaudeResponse = await response.json();
    
    // Extract the text content from the response
    const textContent = data.content.find(content => content.type === 'text');
    
    if (!textContent) {
      throw new Error('No text content found in Claude API response');
    }

    return textContent.text;

  } catch (error) {
    console.error('Error calling Claude API:', error);
    throw error;
  }
}

// Utility function to test the API connection
export async function testClaudeConnection(): Promise<boolean> {
  try {
    const response = await callClaudeAPI('Hello, this is a test message.');
    return response.length > 0;
  } catch (error) {
    console.error('Claude API connection test failed:', error);
    return false;
  }
} 